* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: rgba(223, 217, 210, .725);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  z-index: 1;
  width: 100%;
  background-color: #452dcc;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form__input {
  max-width: 320px;
  font: inherit;
  background-color: #fff;
  border: none;
  padding: 8px;
}

.search-form__btn-submit {
  cursor: pointer;
  text-align: center;
  border: none;
  align-items: center;
  padding: 10px 28px;
}

.gallery {
  flex-wrap: wrap;
  gap: 30px;
  margin-top: 70px;
  padding: 15px;
  display: flex;
}

.photo-card {
  width: calc(25% - 22.5px);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-link {
  height: 200px;
  text-decoration: none;
  display: block;
}

.photo-link:hover {
  cursor: zoom-in;
}

.photo-link img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.photo-link img:hover {
  transform: scale(1.03);
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  margin-top: 8px;
  margin-bottom: 5px;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #3053bb;
  border: none;
  border-radius: 4px;
  justify-content: center;
  margin: 20px auto 25px;
  padding: 10px 28px;
  display: flex;
}

.visibility-hidden {
  display: none;
}

